export default {
    props: {
        default_value: {
            default: 1
        }
        ,
        unit: {
            type: String,
            default: ''
        },
        input_class: {
            type: String,
            default: ''
        },
        field_name: {
            type: String,
            default: ''
        } ,
        max : {
            default : 10000
        } ,
        min : {
            default : 1
        } ,
        step : {
            default : 1
        } ,
    },
    data: function () {
        return {
            extra_txt: '',
            value: ''
        }
    },

    methods: {
        inc : function(){
            if(this.value+ this.step <=  this.max  ){
                this.value += this.step ;
            }
        },
        dec : function(){
            if(this.value - this.step >= this.min){
                this.value -= this.step;
            }

        },
        out_put: function (arr) {
            console.log('mhcms out:', arr);
            this.$emit('updated', arr)
        },
    },

    created: function () {
        this.value = this.default_value;

        if (this.value) {
            let arr = [this.field_name, this.value];
            this.out_put(arr)
        }

    } ,
    watch : {
        value : function (new_val) {
            let arr = [this.field_name, this.value];
            this.out_put(arr)
        }
    }
}
